
import React, { useState, useEffect, useReducer } from 'react';
import { Tooltip, Button } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
// import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Input from '@vkontakte/vkui/dist/components/Input/Input';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import FormLayoutGroup from '@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow';
import ModalRoot from '@vkontakte/vkui/dist/components/ModalRoot/ModalRoot';
import ModalCard from '@vkontakte/vkui/dist/components/ModalCard/ModalCard';
import Icon56MoneyTransferOutline from '@vkontakte/icons/dist/56/money_transfer_outline';
import Icon20Info from '@vkontakte/icons/dist/20/info';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import { Spinner } from '@vkontakte/vkui';

import styles from "../styles/custom-styles.sass";
import ListUsers from './ListUsers';
import Shop from './Shop';
import Inventory from './Inventory';




const Home = ({ id, go, fetchedUser, tabShop, tabHome, tabTop, activeTab,
	dataUsers, progressOnChange, dispatch, state, story, addGroup, incDec,
	openBase, dataShop, openPay, inventoryOpen, inventory, dataInventory,
	getUser, durkaCoins, shisoids, transfer, targetId, onChangeHandlerId,
	onChangeHandlerAmount, amount, activeModal, openTransfer, snackbar,
	userPlace, rating, scheme, openInfo, buyItem, openBuyDurkaCoins, openBuyItem,
	$useItem, targetIdInventory, openUseItem, openHowItWorks, ranks, goTop, scrollUp
}) => {




	function getRankByRating($rating) {
		const rank = ranks.find(({ ratingMin, ratingMax }) => (
			$rating <= (ratingMax || Infinity) && $rating >= ratingMin
		));

		return rank
			? rank.name
			: null
			;
	}

	const renderUsers = dataUsers.map((item, index) => {
		return (
			<ListUsers
				key={index}
				id={index + 1}
				idVk={item.id}
				photoURL={item.photoURL}
				firstName={item.firstName}
				lastName={item.lastName}
				rating={item.rating}
				amount={item.durkaCoins}
				ranks={ranks}
			/>
		);
	});

	const renderShop = dataShop.map((item, index) => {
		return (
			<Shop
				key={index}
				id={item.id}
				name={item.name}
				price={item.price}
				icon={item.icon}
				openPay={openPay}
				openInfo={openInfo}
				transfer={transfer}
				buyItem={buyItem}
				openBuyItem={openBuyItem}
				scheme={scheme}

			/>
		);
	});

	const renderInventory = dataInventory.map((item, index) => {
		return (
			<Inventory
				key={index}
				id={item.id}
				name={item.name}
				price={item.price}
				icon={item.icon}
				count={item.count}
				openPay={openPay}
				openInfo={openInfo}
				transfer={transfer}
				buyItem={buyItem}
				openBuyItem={openBuyItem}
				$useItem={$useItem}
				targetIdInventory={targetIdInventory}
				openUseItem={openUseItem}
				scheme={scheme}
			/>
		);
	});

	return (
		<Panel id={id}>
			{/* <ModalRoot activeModal={this.state.activeModal}> */}
			<PanelHeader>ДУРКА</PanelHeader>

			<FixedLayout vertical="top" filled={true}>
				<Tabs >
					<TabsItem
						onClick={goTop} data-to="top"
					// selected={activeTab === 'top'}
					>
						ТОП
                    </TabsItem>
					<TabsItem
						onClick={tabHome}
						selected={activeTab === 'home'}
					>
						Профиль
                    </TabsItem>
					<TabsItem
						onClick={tabShop}
						selected={activeTab === 'shop'}
					>
						Магазин
                    </TabsItem>
				</Tabs>
			</FixedLayout>

			{
				!rating
					?
					<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
						<Spinner size="large" style={{ marginTop: 90 }} />
					</div>
					:
					<>
						{activeTab === 'top'
							?
							<>
								<Div className="headerTop">
									Ваше место: {userPlace}
									<>
										&#12288;{`Ваш Рейтинг:`}
										<Icon16Fire className="SUKA" fill="red" />

										{rating}
									</>
								</Div>

								<Div>
									{renderUsers}
								</Div>
							</>
							:
							''
						}

						{activeTab === 'home'
							?
							<>
								<Div className="main">
									{/* <Div className="flexStart"> */}
									{fetchedUser &&
										<Div>
											<Cell
												before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} size={120} /> : null}
											>
											</Cell>
											<Div className="divPadding">
												{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
											</Div>
										</Div>
									}

								</Div>
								<Div className="mainLeft">
									<Div className="divPaddingLeft">

										<Div className="divFlexInfo">
											<Icon16Fire fill="red" className="iconInfo" />
											<Div className="divInfoRating">
												Рейтинг: {rating}
											</Div>
										</Div>

										<Div className="divFlexInfo">
											<Icon20Info onClick={go} data-to="rank-info" className="iconInfo" fill="#0000FF" />
											<Div className="divInfoRank">
												Звание: {getRankByRating(rating)}
												<Link className="linkInfo" onClick={go} data-to="rank-info">
													Подробнее
                                    </Link>
											</Div>
										</Div>

									</Div>
									<Div className="divPaddingLeft">
										Привлечено шизоидов: {shisoids}
									</Div>
									<Div className="divPaddingLeft">
										DurkaCoins: {durkaCoins}
									</Div>
								</Div>



								<Div className="divBtn">
									<Button
										mode="commerce"
										size="xl"
										stretched
										onClick={openBase}
										className={scheme === "bright_light" ? "btnBlack" : "btnGray"}
									>
										Привлечь шизоидов
                                    </Button>


									<Div className="flexBtn">
										<Button
											className={scheme === "bright_light" ? "buttonPaymentBlackLeft" : "buttonPaymentGrayLeft"}
											mode="commerce"
											onClick={addGroup}
											size="xl"
											stretched
										>
											Подписаться
                            </Button>
										<Button
											className={scheme === "bright_light" ? "buttonPaymentBlackRight" : "buttonPaymentGrayRight"}
											mode="commerce"
											onClick={go}
											data-to="how-it-works"
											size="xl"
											stretched
										>
											Правила
                            </Button>

									</Div>


									<Div className="divFlex">
										DurkaCoins
						</Div>

									<Div className="flexBtn">

										<Button
											className={scheme === "bright_light" ? "buttonPaymentBlackLeft" : "buttonPaymentGrayLeft"}
											mode="commerce"
											onClick={openTransfer}
											size="xl"
											stretched
										>
											Перевести
                                        </Button>


										<Button
											className={scheme === "bright_light" ? "buttonPaymentBlackRight" : "buttonPaymentGrayRight"}
											mode="commerce"
											onClick={openBuyDurkaCoins}
											size="xl"
											stretched
										>
											Купить
                                        </Button>

									</Div>

									{/* <Button
										mode="commerce"
										size="xl"
										stretched
										onClick={openBase}
										className={scheme === "bright_light" ? "btnBlackHistoty" : "btnGrayHistoty"}
									>
										История операций
                                    </Button> */}


									<Div className="mainInventory">
										{inventory
											?
											<Link
												className="inventory"
												id={id}
												onClick={inventoryOpen}
												mode="tertiary"
											>
												Открыть инвентарь
                                            </Link>
											:
											<>
												<Link
													className="inventory"
													id={id}
													onClick={inventoryOpen}
													mode="tertiary"
												>
													Закрыть инвентарь
					                            </Link>

												{renderInventory
													?

													<>
														{renderInventory}
														<Div>
															<Link
																id={id}
																onClick={scrollUp}
																mode="tertiary"
															>
																Вверх
					                                    </Link>
														</Div>
													</>
													:
													<Div>
														У Вас еще нету товаров из <> </>
														<Link
															id={id}
															onClick={tabShop}
															mode="tertiary"
														>
															Магазина.
					                                    </Link>
													</Div>
												}





											</>
										}
									</Div>



								</Div>





							</>
							: ''
						}



						{activeTab === 'shop'
							?
							<Div className="shop">
								<Div className="divShop">
									Баланс DurkaCoins: {durkaCoins}
								</Div>
								{renderShop}
							</Div>
							:
							''
						}


						{snackbar}
					</>
			}
		</Panel>
	);
};

export default Home;
